<template>
    <div class="about">
        <img :src="baseInfo.banner" class="img"/>
        <div class="navigation" v-if="baseInfo.title !== baseInfo.home.title">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7 pointer" @click="navigator">{{ baseInfo.home.title }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ baseInfo.title }}</div>
        </div>
        <div class="introduce">
            <div class="title">{{ $t('companyIntroduction') }}</div>
            <div class="title-en">COMPANY INTRODUCTION</div>
            <div class="card">
                <div class="block"></div>
                {{  $indent(info.introduction.about_us) }}
            </div>
        </div>
        <div class="culture">
            <div class="culture-card">
                <div class="cu-title">{{ $t('companyCulture') }}</div>
                <div class="title-en">CORPORATE CULTURE</div>
                <div class="between">
                    <div>
                        <div style="padding-bottom: 20px;">
                            <div class="li">·{{ $t('missionOfCompany') }}</div>
                            <div class="li-text">{{ info.culture.mission }}</div>
                        </div>
                        <div style="padding-bottom: 20px;">
                            <div class="li">·{{ $t('companyValues') }}</div>
                            <div class="li-text">
                                <div style="margin-bottom: 4px;">{{ info.culture.values }}</div>
                            </div>
                        </div>
                        <div>
                            <div class="li">·{{ $t('corporateVision') }}</div>
                            <div class="li-text">{{ info.culture.vision }}</div>
                        </div>
                    </div>
                    <img :src="info.culture.culture_images" class="cu-img">
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { useRouter, useRoute } from "vue-router"
import { useStore } from "vuex";
import { computed, ref } from 'vue'

    const router = useRouter();
    const route = useRoute();
    const store = useStore();
    const info = computed(() => {
        return store.state.info
    });
    const baseInfo = computed(() => {
        if (store.state.info.navigation) {
            let obj = store.state.info.navigation.find(el => el.router === route.path)
            document.title = `${ obj.title }-${ info.value.company_name }`
            return {
                banner: obj.banner,
                title: obj.title,
                home: store.state.info.navigation[0]
            }
        }
        return {
            banner: '',
            title: '',
            home: {}
        }
    });
    const navigator = () =>{
        router.push(baseInfo.value.home.router)
    }
</script>
